import React, { Component } from "react";
import TextField from "material-ui/TextField";
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn
} from "material-ui/Table";
import Filters from "./filters";
import "./App.css";
import FloatingActionButton from "material-ui/FloatingActionButton";
import ContentAdd from "material-ui/svg-icons/content/add";
import AutoComplete from "material-ui/AutoComplete";
import Drawer from "material-ui/Drawer";
import MenuItem from "material-ui/MenuItem";
import RaisedButton from "material-ui/RaisedButton";
import Checkbox from "material-ui/Checkbox";
import { List, ListItem } from "material-ui/List";
import Subheader from "material-ui/Subheader";
import SelectField from "material-ui/SelectField";
const searchFields = [
  { name: "Description", id: "description" },
  { name: "Transcripts", id: "transcripts" },
  { name: "Biotype", id: "biotype" },
  { name: "Phenotypes", id: "phenotypes" }
];

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      genes: [],
      cols: [],
      rows: [],
      genomesList: [],
      loading: false,
      genomes: [],
      values: [],
      activeCheckboxes: [],
      open: false,
      selectedGenome: "",
      selectedFields: "",
      userSelectedGenome: "",
      searchFields: [
        { name: "Description", id: "description" },
        { name: "Biotype", id: "biotype" },
        { name: "Taxon ID", id: "taxon_id" },
        { name: "Start", id: "start" },
        { name: "Strand", id: "strand" }
      ]
    };
    this.searchGene = this.searchGene.bind(this);
    this.filterResults = this.filterResults.bind(this);
    this.getGenomes = this.getGenomes.bind(this);
    this.searchGeneMain = this.searchGeneMain.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.updateFilters = this.updateFilters.bind(this);
    this.selectGenome = this.selectGenome.bind(this);
  }
  handleToggle = () => {
    this.setState({ open: !this.state.open });
  };
  getGenomes(search) {
    console.log("Getting genomes with: ", search);
    if (search.length > 2) {
      this.sendRequest(
        "/genomes/select?limit=99&query=" + search
      ).then(result => {
        this.setState({
          genomesList: result.results.map(x => {
            return x.organism.scientific_name;
          })
        });
      });
    }
  }

  searchGeneMain() {
    console.log("SEARCHING");
    this.searchGene({}, this.state.userEnteredGene);
  }

  sendRequest(url) {
    url = "http://gti-es-0.ebi.ac.uk:8080/api" + url;
    console.log("sending request", url);
    return fetch(url).then(a => a.json());
  }

  selectGenome(genome) {
    let newrows = this.state.rows.filter(x => {
      if (
        x.genome.toLowerCase() ===
        genome
          .trim()
          .toLowerCase()
          .replace(" ", "_")
      )
        return x;
    });
    this.setState({
      rows: newrows
    });
  }

  //Deprecated
  filterResults(search) {
    console.log("filtering", search);
    let genes = this.state.genes.results.filter(gene => {
      return gene.genome.indexOf(search) > -1 ? gene : false;
    });
    this.setState({
      rows: genes
    });
  }
  searchGene(e, value) {
    this.setState({
      loading: true,
      userEnteredGene: value
    });
    const URL = "http://gti-es-0.ebi.ac.uk:8080/api";
    let url =
      URL + '/genes/fetch?query={"name":"' + value + '"}&fields=id,name,genome';
    if (this.state.selectedFields.length > 2)
      url = url + "," + this.state.selectedFields;
    console.log("Fetching", url);
    fetch(url)
      .then(a => a.json())
      .then(json => {
        console.log(json);
        this.setState({
          loading: false,
          genes: json,
          cols: json.fields,
          rows: json.results
        });
      });
  }

  componentDidMount() {
    this.searchGene({}, "BRCA2");
  }

  updateFilters = (event, index, values) => {
    let filterString = values.join(",");
    this.setState({
      selectedFields: filterString
    });
    this.setState({ values });
  };

  filterItems(values) {
    return this.state.searchFields.map(item => (
      <MenuItem
        key={item.id}
        insetChildren={true}
        checked={values.includes(item.id)}
        value={item.id}
        primaryText={item.name}
      />
    ));
  }

  render() {
    let results = "";
    if (this.state.cols) {
      results = this.state.cols.map(x => {
        //console.log(x);
        return this.state.rows.map(y => {
          //console.log(y);
          return (
            <div key={y.id} className="item">
              <p>
                <em>ID:</em>
                {y.id}
              </p>

              <p>
                <em>Name:</em>
                {y.name}
              </p>

              <p>
                <em>Genome:</em>
                {y.genome}
              </p>
              {this.state.values.map((item, index) => (
                <p key={index}>
                  <em>{item}:</em>
                  {y[item]}
                </p>
              ))}
            </div>
          );
        });
      });
    }

    return (
      <div>
        <TextField
          style={{ margin: "20px 50px" }}
          defaultValue="BRCA2"
          onChange={this.searchGene}
          hintText="Search genes by name"
        />
        <div />
        <br />
        <div id="config" style={{ display: "flex", margin: "20px" }}>
          <AutoComplete
            style={{ margin: "0 20px 0 0" }}
            hintText="Genome"
            filter={AutoComplete.fuzzyFilter}
            dataSource={this.state.genomesList}
            onUpdateInput={this.getGenomes}
            onNewRequest={this.selectGenome}
          />

          <SelectField
            style={{ margin: "0 20px 0 0" }}
            className="customSwitcherField"
            multiple={true}
            hintText="Select filters"
            value={this.state.values}
            onChange={this.updateFilters}>
            {this.filterItems(this.state.values)}
          </SelectField>

          <FloatingActionButton onClick={this.searchGeneMain}>
            <ContentAdd />
          </FloatingActionButton>
        </div>

        {this.state.loading > 0 && <p>Spinning round</p>}

        <div id="results">{results}</div>
      </div>
    );
  }
}

export default App;



